import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { deleteNoteCategory } from '../../actions/noteCategoryActions';

class NoteCategoryItem extends Component {
    onDeleteClick(id) {
        if (window.confirm("Bn c mun xa khng ?")) {
            this.props.deleteNoteCategory(id);

        }
    }
    render() {
        const { note_category, showActions } = this.props;
        return (
            <tr>
                <td>{note_category.name}</td>
                <td>
                    {showActions ? (
                        <div className="hidden-sm hidden-xs btn-group">
                            <button className="btn btn-xs btn-success">
                                <Link to={`/show-note_categories/${note_category._id}`} style={{ color: '#FFF' }} >
                                    <i className="ace-icon fa fa-eye bigger-120"></i>
                                </Link>
                            </button>
                            <button className="btn btn-xs btn-info">
                                <Link to={`/edit-note_categories/${note_category._id}`} style={{ color: '#FFF' }} >
                                    <i className="ace-icon fa fa-pencil bigger-120"></i>
                                </Link>
                            </button>
                            <button className="btn btn-xs btn-danger">
                                <i className="ace-icon fa fa-trash-o bigger-120" onClick={this.onDeleteClick.bind(this, note_category._id)}></i>
                            </button>
                        </div>
                    ) : (
                            <button className="btn btn-xs btn-success">
                                <Link to={`/show-note_categories/${note_category._id}`} style={{ color: '#FFF' }} >
                                    <i className="ace-icon fa fa-eye bigger-120"></i>
                                </Link>
                            </button>
                        )
                    }
                </td>
            </tr >
        )
    }
}
NoteCategoryItem.defaultProps = {
    showActions: true
}
NoteCategoryItem.propTypes = {
    note_category: PropTypes.object.isRequired,
    deleteNoteCategory: PropTypes.func.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { deleteNoteCategory })(NoteCategoryItem);

