import React, { Component } from 'react'
import Breadcrumb from '../common/Breadcrumb'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getExperience } from '../../actions/projectActions';
import Spinner from '../common/Spinner';
import isEmpty from '../../utils/isEmpty';
import placeholder from '../../img/placeholder.png';
import Moment from 'react-moment';
class ShowExperience extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            date: '',
            timesize: '',
            link: '',
            company: '',
            technology: '',
            image: '',
            content: '',
            loading: true,
            errors: {}
        };
    }
    componentDidMount() {
        this.props.getExperience(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if ((!isEmpty(nextProps.project.project))) {
            this.setState({
                loading: false
            })
        }
        if (nextProps.project.project) {
            const project = nextProps.project.project;
            project.name = !isEmpty(project.name) ? project.name : '';
            project.teamsize = !isEmpty(project.teamsize) ? String(project.teamsize) : '';
            project.image = !isEmpty(project.image) ? project.image : placeholder;
            project.company = !isEmpty(project.company) ? project.company : '';
            project.link = !isEmpty(project.link) ? project.link : '';
            project.content = !isEmpty(project.content) ? project.content : '';
            project.date = !isEmpty(project.date) ? project.date : '';
            project.technology = !isEmpty(project.technology) ? project.technology : '';
            this.setState({
                name: project.name,
                date: project.date,
                teamsize: project.teamsize,
                image: project.image,
                link: project.link,
                company: project.company,
                content: project.content,
                technology: project.technology
            })
        }
    }
    render() {
        let Content;
        if (this.state.loading) {
            Content = <Spinner />
        } else {
            Content = (
                <div className="row">
                    <div className="col-xs-12">
                        <div>
                            <div id="user-profile-2" className="user-profile">
                                <div className="tabbable">

                                    <div className="tab-content no-border padding-24">
                                        <div className="tab-pane in active">
                                            <div className="row">
                                                <div className="col-xs-12 col-sm-3 center">
                                                    <span className="profile-picture">
                                                        <img className="editable img-responsive" src={this.state.image} alt={this.state.name} width="300px;" height="300px;" />
                                                    </span>

                                                    <div className="space space-4"></div>
                                                    <a href={this.state.link} target="_blank" rel="noopener noreferrer" className="btn btn-sm btn-block btn-primary">
                                                        <i className="ace-icon fa fa-envelope-o bigger-110"></i>
                                                        <span className="bigger-110">Go to Website</span>
                                                    </a>
                                                </div>

                                                <div className="col-xs-12 col-sm-9">
                                                    <h4 className="blue">
                                                        <span className="middle">{this.state.name}</span>
                                                    </h4>

                                                    <div className="profile-user-info">
                                                        <div className="profile-info-row">
                                                            <div className="profile-info-name"> Company </div>

                                                            <div className="profile-info-value">
                                                                <span>{this.state.company}</span>
                                                            </div>
                                                        </div>

                                                        <div className="profile-info-row">
                                                            <div className="profile-info-name"> Team size </div>

                                                            <div className="profile-info-value">
                                                                <span>{this.state.teamsize}</span>
                                                            </div>
                                                        </div>
                                                        <div className="profile-info-row">
                                                            <div className="profile-info-name"> Date start </div>

                                                            <div className="profile-info-value">
                                                                <span>{<Moment format="DD/MM/YYYY">{this.state.date}</Moment>}</span>
                                                            </div>
                                                        </div>
                                                        <div className="profile-info-row">
                                                            <div className="profile-info-name"> Technology </div>

                                                            <div className="profile-info-value">
                                                                <span>{this.state.technology}</span>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div className="hr hr-8 dotted"></div>
                                                </div>
                                            </div>

                                            <div className="space-20"></div>

                                            <div className="row">
                                                <div className="col-xs-12 col-sm-6">
                                                    <div className="widget-box transparent">
                                                        <div className="widget-header widget-header-small">
                                                            <h4 className="widget-title smaller">
                                                                <i className="ace-icon fa fa-check-square-o bigger-110"></i>
                                                                Content
															</h4>
                                                        </div>

                                                        <div className="widget-body">
                                                            <div className="widget-main">
                                                                <div dangerouslySetInnerHTML={{ __html: this.state.content }} />
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        return (
            <div>
                <Breadcrumb name={this.state.name} />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            {this.state.name}
                        </h1>
                    </div>
                    {Content}
                </div>
            </div>
        )
    }
}
ShowExperience.propTypes = {
    getExperience: PropTypes.func.isRequired,
    project: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    project: state.project
});
export default connect(mapStateToProps, { getExperience })(ShowExperience);
