import RequestService from '../utils/requestService';
import {
    CLEAR_ERRORS,
    PROJECTS_LOADING,
    GET_ERRORS,
    GET_PROJECTS,
    GET_PROJECT,
    DELETE_PROJECT
} from './types';
//Add Project
export const createProject = (data, history) => dispatch => {
    RequestService
        .post("/api/projects", data)
        .then(res => history.push('/projects'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Get Projects
export const getProjects = () => dispatch => {
    dispatch(setProjectLoading());
    RequestService
        .get('/api/projects')
        .then(res =>
            dispatch({
                type: GET_PROJECTS,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_PROJECTS,
                payload: null
            }))
}
//Detail Projects
export const getProject = (id) => dispatch => {
    dispatch(setProjectLoading());
    RequestService
        .get(`/api/projects/${id}`)
        .then(res =>
            dispatch({
                type: GET_PROJECT,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_PROJECT,
                payload: null
            }))
}
//Detail Experience
export const getExperience = (id) => dispatch => {
    dispatch(setProjectLoading());
    RequestService
        .get(`/api/projects/detail/${id}`)
        .then(res =>
            dispatch({
                type: GET_PROJECT,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_PROJECT,
                payload: null
            }))
}
//Update Project
export const updateProject = (data, history, id) => dispatch => {
    RequestService
        .put(`/api/projects/${id}`, data)
        .then(res => history.push('/projects'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Status Works
export const changeWork = (id, idProject) => dispatch => {
    dispatch(setProjectLoading());
    RequestService
        .get(`/api/works/change/${id}`)
        .then(res =>
            dispatch(getProject(idProject))
        )
        .catch(err =>
            dispatch(getProject(idProject))
        )
}
//Delete Projects
export const deleteProject = (id) => dispatch => {
    RequestService
        .del(`/api/projects/${id}`)
        .then(res =>
            dispatch({
                type: DELETE_PROJECT,
                payload: id
            }))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Status Projects
export const changeProject = (id) => dispatch => {
    dispatch(setProjectLoading());
    RequestService
        .get(`/api/projects/change/${id}`)
        .then(res =>
            dispatch(getProjects())
        )
        .catch(err =>
            dispatch(getProjects())
        )
}
//Set loadiing state
export const setProjectLoading = () => {
    return {
        type: PROJECTS_LOADING
    }
}
//Clear errors
export const clearErrors = () => {
    return {
        type: CLEAR_ERRORS
    }
}

