import React, { Component } from 'react';
import { Provider } from 'react-redux';
import store from './store';
import jwt_decode from 'jwt-decode'
import setAuthToken from './utils/setAuthToken'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { setCurrentUser, logoutUser } from './actions/authActions';
import PrivateRoute from './components/common/PrivateRoute';
//Layouts
import Footer from './components/layouts/Footer';
import Header from './components/layouts/Header';
import Sidebar from './components/layouts/Sidebar';
//Login
import Login from './components/login/Login';
//Home
import Home from './components/home/Home';
//Timeline
import Timelines from './components/timelines/Timelines';
import CreateTimeline from './components/timelines/CreateTimeline';
import EditTimeline from './components/timelines/EditTimeline';
//Not found
import NotFound from './components/not-found/NotFound';
//Note Category
import NoteCategories from './components/note-category/NoteCategories';
import CreateNoteCategory from './components/note-category/CreateNoteCategory';
import EditNoteCategory from './components/note-category/EditNoteCategory';
import ShowNoteCategory from './components/note-category/ShowNoteCategory';
//Note
import CreateNote from './components/note/CreateNote';
import EditNote from './components/note/EditNote';
import ShowNote from './components/note/ShowNote';
//Project
import Project from './components/project/Projects';
import CreateProject from './components/project/CreateProject';
import EditProject from './components/project/EditProject';
import ShowProject from './components/project/ShowProject';
//Work
import CreateWork from './components/work/CreateWork';
import EditWork from './components/work/EditWork';
//Experience
import Experience from './components/experience/Experiences';
import ShowExperience from './components/experience/ShowExperience';
//Check for token
if (localStorage.jwtToken) {
  //Set auth token header auth
  setAuthToken(localStorage.jwtToken);
  try {
    //Decode token and get user info and exp
    const decoded = jwt_decode(localStorage.jwtToken);
    if (decoded.exp) {
      //Set user and isAuthenticated
      store.dispatch(setCurrentUser(decoded));
      //Check for expired token
      const currentTime = Date.now() / 1000;
      if (decoded.exp < currentTime) {
        //Logout user
        store.dispatch(logoutUser());
        //Redirect to not-found
        window.location.href = '/not-found';
      }
    } else {
      //Logout user
      store.dispatch(logoutUser());
      //Redirect to not-found
      window.location.href = '/not-found';
    }
  } catch (error) {
    console.log('Error');
  }
}
class App extends Component {
  render() {
    return (
      <Provider store={store} >
        <Router>
          <div>
            <Header />
            <div className="main-container ace-save-state" id="main-container">
              <div id="sidebar" className="sidebar responsive ace-save-state">
                <div>
                  <Sidebar />
                  <div className="sidebar-toggle sidebar-collapse" id="sidebar-collapse">
                    <i id="sidebar-toggle-icon"
                      className="ace-icon fa fa-angle-double-left ace-save-state"
                      data-icon1="ace-icon fa fa-angle-double-left"
                      data-icon2="ace-icon fa fa-angle-double-right"></i>
                  </div>
                </div>
              </div>
              <div className="main-content">
                <div className="main-content-inner">
                  <Route exact path="/" component={Home} />
                  <Route exact path="/experiences" component={Experience} />
                  <Route exact path="/show-experiences/:id" component={ShowExperience} />
                  <Route exact path="/show-note_categories/:id" component={ShowNoteCategory} />
                  <Route exact path="/show-notes/:id" component={ShowNote} />
                  <Route exact path="/note_categories" component={NoteCategories} />
                  <Route exact path="/login" component={Login} />
                  <Switch>
                    <PrivateRoute exact path="/timelines" component={Timelines} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/create-timelines" component={CreateTimeline} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/edit-timelines/:id" component={EditTimeline} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/create-note_categories" component={CreateNoteCategory} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/edit-note_categories/:id" component={EditNoteCategory} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/create-notes/:id" component={CreateNote} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/edit-notes/:id" component={EditNote} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/create-projects" component={CreateProject} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/edit-projects/:id" component={EditProject} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/projects" component={Project} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/show-projects/:id" component={ShowProject} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/create-works/:id" component={CreateWork} />
                  </Switch>
                  <Switch>
                    <PrivateRoute exact path="/edit-works/:id" component={EditWork} />
                  </Switch>
                  <Route exact path="/not-found" component={NotFound} />
                </div>
              </div >
              <Footer />
            </div >
          </div >
        </Router>
      </Provider>
    );
  }
}

export default App;
