import RequestService from '../utils/requestService';
import {
    CLEAR_ERRORS,
    NOTECATEGORIES_LOADING,
    GET_ERRORS,
    GET_NOTECATEGORIES,
    GET_NOTECATEGORY,
    DELETE_NOTECATEGORY
} from './types';
//Add NoteCategory
export const createNoteCategory = (data, history) => dispatch => {
    RequestService
        .post("/api/note_categories", data)
        .then(res => history.push('/note_categories'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Get oteCategories
export const getNoteCategories = () => dispatch => {
    dispatch(setNoteCategoryLoading());
    RequestService
        .get('/api/note_categories')
        .then(res =>
            dispatch({
                type: GET_NOTECATEGORIES,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_NOTECATEGORIES,
                payload: null
            }))
}
//Detail NoteCategory
export const getNoteCategory = (id) => dispatch => {
    dispatch(setNoteCategoryLoading());
    RequestService
        .get(`/api/note_categories/${id}`)
        .then(res =>
            dispatch({
                type: GET_NOTECATEGORY,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_NOTECATEGORY,
                payload: null
            }))
}
//Update NoteCategory
export const updateNoteCategory = (data, history, id) => dispatch => {
    RequestService
        .put(`/api/note_categories/${id}`, data)
        .then(res => history.push('/note_categories'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Delete NoteCategory
export const deleteNoteCategory = id => dispatch => {
    RequestService
        .del(`/api/note_categories/${id}`)
        .then(res =>
            dispatch({
                type: DELETE_NOTECATEGORY,
                payload: id
            }))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}


//Set loadiing state
export const setNoteCategoryLoading = () => {
    return {
        type: NOTECATEGORIES_LOADING
    }
}
//Clear errors
export const clearErrors = () => {
    return {
        type: CLEAR_ERRORS
    }
}

