import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import Moment from 'react-moment';
import { deleteTimeline } from '../../actions/timelineActions';

class TimelineItem extends Component {
    onDeleteClick(id) {
        if (window.confirm("Bn c mun xa khng ?")) {
            this.props.deleteTimeline(id);
        }
    }
    render() {
        const { timeline, showActions } = this.props;
        return (
            <div className="timeline-container">
                <div className="timeline-label">
                    <span className="label label-grey arrowed-in-right label-lg">
                        {<Moment format="DD/MM/YYYY">{timeline.date}</Moment>}
                    </span>
                </div>

                <div className="timeline-items">
                    <div className="timeline-item clearfix">
                        <div className="timeline-info">
                            <i className={classnames('timeline-indicator ace-icon btn btn-primary no-hover green', timeline.logo)}></i>
                        </div>

                        <div className="widget-box transparent">
                            <div className="widget-header widget-header-small">
                                <h5 className="widget-title smaller">{timeline.name}</h5>
                            </div>

                            <div className="widget-body">
                                <div className="widget-main">
                                    {timeline.content}
                                    <div className="space-6"></div>

                                    <div className="widget-toolbox clearfix">
                                        {showActions ? (<div className="pull-right action-buttons">
                                            <i className="ace-icon fa fa-times red bigger-125" onClick={this.onDeleteClick.bind(this, timeline._id)}></i>
                                            <Link to={`/edit-timelines/${timeline._id}`}>
                                                <i className="ace-icon fa fa-pencil blue bigger-125"></i>
                                            </Link>
                                        </div>) : null}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
TimelineItem.defaultProps = {
    showActions: true
}

TimelineItem.propTypes = {
    timeline: PropTypes.object.isRequired,
    deleteTimeline: PropTypes.func.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { deleteTimeline })(TimelineItem);

