import React, { Component } from 'react';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getNote } from '../../actions/noteActions';
import Spinner from '../common/Spinner';
import isEmpty from '../../utils/isEmpty';
class ShowNote extends Component {
    constructor() {
        super();
        this.state = {
            title: '',
            content: '',
            loading: true,
            errors: {}
        };
        this.goBack = this.goBack.bind(this);
    }
    goBack() {
        this.props.history.goBack();
    }
    componentDidMount() {
        this.props.getNote(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if ((!isEmpty(nextProps.note.note))) {
            this.setState({
                loading: false
            })
        }
        if (nextProps.note.note) {
            const note = nextProps.note.note;
            note.title = !isEmpty(note.title) ? note.title : '';
            note.content = !isEmpty(note.content) ? note.content : '';
            this.setState({
                title: note.title,
                content: note.content
            })
        }
    }
    render() {
        let Content;
        if (this.state.loading) {
            Content = <Spinner />
        } else {
            if (this.state.content === '') {
                Content = (
                    <b>Cha c ni dung</b>
                )
            } else {
                Content = (
                    <div dangerouslySetInnerHTML={{ __html: this.state.content }} />
                )
            }
        }
        return (
            <div>
                <Breadcrumb name={this.state.title} />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">{this.state.title}</h4>
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>

                    </div>
                    <button className="btn btn-warning" onClick={this.goBack}>
                        <i className="ace-icon fa fa-arrow-left bigger-110"></i>
                        Quay v
                    </button>
                </div>
            </div >
        )
    }
}
ShowNote.propTypes = {
    getNote: PropTypes.func.isRequired,
    note: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    note: state.note
});
export default connect(mapStateToProps, { getNote })(ShowNote);