import RequestService from '../utils/requestService';
import {
    CLEAR_ERRORS,
    WORKS_LOADING,
    GET_ERRORS,
    GET_WORK,
    DELETE_WORK
} from './types';
//Add Work
export const createWork = (data, history) => dispatch => {
    RequestService
        .post("/api/works", data)
        .then(res => history.push(`/show-projects/${data.idProject}`))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Detail Works
export const getWork = (id) => dispatch => {
    dispatch(setWorkLoading());
    RequestService
        .get(`/api/works/${id}`)
        .then(res =>
            dispatch({
                type: GET_WORK,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_WORK,
                payload: null
            }))
}
//Update Work
export const updateWork = (data, history, id) => dispatch => {
    RequestService
        .put(`/api/works/${id}`, data)
        .then(res => history.push('/projects'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Delete Works
export const deleteWork = (data, id) => dispatch => {
    RequestService
        .del(`/api/works/${id}`, data)
        .then(res =>
            dispatch({
                type: DELETE_WORK,
                payload: id
            }))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}


//Set loadiing state
export const setWorkLoading = () => {
    return {
        type: WORKS_LOADING
    }
}
//Clear errors
export const clearErrors = () => {
    return {
        type: CLEAR_ERRORS
    }
}

