import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { getProject, updateProject } from '../../actions/projectActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import isEmpty from '../../utils/isEmpty';
import TextImage from '../common/TextImage';
import placeholder from '../../img/placeholder.png';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';
class EditProject extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            date: '',
            teamsize: '',
            image: '',
            link: '',
            company: '',
            technology: '',
            content: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        if (e.target.type === 'checkbox') {
            this.setState({
                status: e.target.checked
            })
        } else {
            this.setState({ [e.target.name]: e.target.value });
        }
    }
    componentDidMount() {
        this.props.getProject(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.project.project) {
            const project = nextProps.project.project;
            project.name = !isEmpty(project.name) ? project.name : '';
            project.teamsize = !isEmpty(project.teamsize) ? String(project.teamsize) : '';
            project.image = !isEmpty(project.image) ? project.image : placeholder;
            project.content = !isEmpty(project.content) ? project.content : '';
            project.company = !isEmpty(project.company) ? project.company : '';
            project.link = !isEmpty(project.link) ? project.link : '';
            project.date = !isEmpty(project.date) ? new Date(project.date).toISOString().split('T')[0] : '';
            project.technology = !isEmpty(project.technology) ? project.technology : '';
            this.setState({
                name: project.name,
                date: project.date,
                teamsize: project.teamsize,
                image: project.image,
                link: project.link,
                company: project.company,
                content: project.content,
                technology: project.technology
            })
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            name: this.state.name,
            date: this.state.date,
            status: this.state.status,
            image: this.state.image,
            teamsize: this.state.teamsize,
            link: this.state.link,
            company: this.state.company,
            technology: this.state.technology,
            content: this.state.content
        }

        this.props.updateProject(Data, this.props.history, this.props.match.params.id);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name={this.state.name} />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            Sa {this.state.name}
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <TextFieldGroup
                                            label="Tn project"
                                            placeholder="Name"
                                            name="name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                        />
                                        <TextFieldGroup
                                            label="Cng ngh s dng"
                                            placeholder="Technology"
                                            name="technology"
                                            value={this.state.technology}
                                            onChange={this.onChange}
                                            error={errors.technology}
                                        />
                                        <TextFieldGroup
                                            label="Link Project"
                                            placeholder="Link"
                                            name="link"
                                            value={this.state.link}
                                            onChange={this.onChange}
                                            error={errors.link}
                                        />
                                        <TextAreaFieldGroup
                                            label="Ni dung"
                                            name="content"
                                            value={this.state.content}
                                            onChange={this.onChange}
                                            error={errors.content}
                                        />
                                    </div>
                                    <div className="col-sm-4 form-actions">
                                        <TextFieldGroup
                                            type="date"
                                            label="Ngy lm project"
                                            placeholder="Date Start"
                                            name="date"
                                            value={this.state.date}
                                            onChange={this.onChange}
                                            error={errors.date}
                                        />
                                        <TextFieldGroup
                                            label="nh project"
                                            placeholder="Image"
                                            name="image"
                                            value={this.state.image}
                                            onChange={this.onChange}
                                            error={errors.image}
                                        />
                                        <TextImage
                                            label="nh"
                                            image={this.state.image}
                                            alt={this.state.name}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Sa
                                        </button>
                                        <Link to="/projects">
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                            </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div >

        )
    }
}
EditProject.propTypes = {
    getProject: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired,
    project: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors,
    project: state.project
});

export default connect(mapStateToProps, { getProject, updateProject })(EditProject);
