import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { createWork } from '../../actions/workActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import TextCheckboxGroup from '../common/TextCheckboxGroup';
class CreateWork extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            date: '',
            status: false,
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        if (e.target.type === 'checkbox') {
            this.setState({
                status: e.target.checked
            })
        } else {
            this.setState({ [e.target.name]: e.target.value });
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            name: this.state.name,
            date: this.state.date,
            status: this.state.status,
            idProject: this.props.match.params.id
        }
        this.props.createWork(Data, this.props.history);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name="To cng vic" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            To cng vic
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <TextFieldGroup
                                            label="Tn cng vic"
                                            placeholder="Name"
                                            name="name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                        />
                                    </div>
                                    <div className="col-sm-4 form-actions">
                                        <TextFieldGroup
                                            type="date"
                                            label="Ngy"
                                            placeholder="Date"
                                            name="date"
                                            value={this.state.date}
                                            onChange={this.onChange}
                                            error={errors.date}
                                        />
                                        <TextCheckboxGroup
                                            label="Trng thi"
                                            name="status"
                                            defaultChecked={this.state.status}
                                            onChange={this.onChange}
                                            error={errors.status}
                                            value={this.state.status}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Thm mi
                                    </button>
                                        <Link to={`/show-projects/${this.props.match.params.id}`}>
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                        </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
CreateWork.propTypes = {
    createWork: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors
});

export default connect(mapStateToProps, { createWork })(CreateWork);
