import React from 'react'
import { Link } from 'react-router-dom';
export default () => {
    return (
        <div id="navbar" className="navbar navbar-default ace-save-state">
            <div className="navbar-container ace-save-state" id="navbar-container">
                <button type="button" className="navbar-toggle menu-toggler pull-left"
                    id="menu-toggler" data-target="#sidebar">
                    <span className="sr-only">Toggle sidebar</span> <span className="icon-bar"></span>
                    <span className="icon-bar"></span> <span className="icon-bar"></span>
                </button>
                <div className="navbar-header pull-left">
                    <Link to="/" className="navbar-brand">
                        <small> <i className="fa fa-leaf"></i>
                            Nguyn Minh Qun 
				        </small>
                    </Link>
                </div>
            </div>
        </div>
    )
}
