import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { deleteNote } from '../../actions/noteActions';

class NoteItem extends Component {
    onDeleteClick(id) {
        const Data = {
            idNoteCategory: this.props.idNoteCategory
        }
        if (window.confirm("Bn c mun xa khng ?")) {
            this.props.deleteNote(Data, id);
        }
    }
    render() {
        const { note, showActions } = this.props;
        return (
            <tr>
                <td>{note.title}</td>
                <td>
                    {showActions ? (
                        <div className="hidden-sm hidden-xs btn-group">
                            <button className="btn btn-xs btn-success">
                                <Link to={`/show-notes/${note._id}`} style={{ color: '#FFF' }} >
                                    <i className="ace-icon fa fa-eye bigger-120"></i>
                                </Link>
                            </button>
                            <button className="btn btn-xs btn-info">
                                <Link to={`/edit-notes/${note._id}`} style={{ color: '#FFF' }} >
                                    <i className="ace-icon fa fa-pencil bigger-120"></i>
                                </Link>
                            </button>
                            <button className="btn btn-xs btn-danger">
                                <i className="ace-icon fa fa-trash-o bigger-120" onClick={this.onDeleteClick.bind(this, note._id)}></i>
                            </button>
                        </div>
                    ) : (
                            <button className="btn btn-xs btn-success">
                                <Link to={`/show-notes/${note._id}`} style={{ color: '#FFF' }} >
                                    <i className="ace-icon fa fa-eye bigger-120"></i>
                                </Link>
                            </button>
                        )
                    }
                </td>
            </tr >
        )
    }
}
NoteItem.defaultProps = {
    showActions: true
}
NoteItem.propTypes = {
    note: PropTypes.object.isRequired,
    deleteNote: PropTypes.func.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { deleteNote })(NoteItem);

