import React, { Component } from 'react';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getProjects } from '../../actions/projectActions';
import Spinner from '../common/Spinner';
import ExperienceFeed from './ExperienceFeed';
class Experiences extends Component {
    componentDidMount() {
        this.props.getProjects();
    }
    render() {
        const { projects, loading } = this.props.project;
        let Content;
        if (projects === null || loading) {
            Content = <Spinner />
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="40%">Name</th>
                            <th width="40%">Image</th>
                            <th width="20%">Link</th>
                        </tr>
                    </thead>

                    <tbody>
                        <ExperienceFeed projects={projects} />
                    </tbody>
                </table>
            )
        }
        return (
            <div>
                <Breadcrumb name="Experiences" />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">Projects</h4>
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Experiences.propTypes = {
    getProjects: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({
    project: state.project
});
export default connect(mapStateToProps, { getProjects })(Experiences);
