import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getTimelines } from '../../actions/timelineActions';
import Spinner from '../common/Spinner';
import TimelineFeed from './TimelineFeed';
class Timelines extends Component {
    componentDidMount() {
        this.props.getTimelines();
    }
    render() {
        const { timelines, loading } = this.props.timeline;
        let timelineContent;
        if (timelines === null || loading) {
            timelineContent = <Spinner />
        } else {
            timelineContent = <TimelineFeed timelines={timelines} />
        }
        return (
            <div>
                <Breadcrumb name="Timelines" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            Timelines
                        </h1>
                    </div>
                    <div className="widget-toolbar no-border">
                        <Link to="/create-timelines" className="btn btn-sm btn-info">
                            <i className="ace-icon fa fa-plus"></i> To mi Timeline
                        </Link>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <div id="timeline-1">
                                <div className="row">
                                    <div className="col-xs-12 col-sm-10 col-sm-offset-1">
                                        {timelineContent}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Timelines.propTypes = {
    getTimelines: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    timeline: state.timeline,
    auth: state.auth
});
export default connect(mapStateToProps, { getTimelines })(Timelines);
