import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getProjects } from '../../actions/projectActions';
import Spinner from '../common/Spinner';
import ProjectFeed from './ProjectFeed';
class Projects extends Component {
    componentDidMount() {
        this.props.getProjects();
    }
    render() {
        const { projects, loading } = this.props.project;
        let Content;
        if (projects === null || loading) {
            Content = <Spinner />
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="25%">Tn</th>
                            <th width="25%">Ngy</th>
                            <th width="25%">Trng thi</th>
                            <th width="25%"></th>
                        </tr>
                    </thead>

                    <tbody>
                        <ProjectFeed projects={projects} />
                    </tbody>
                </table>
            )
        }
        return (
            <div>
                <Breadcrumb name="Ghi ch" />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">Danh mc project</h4>
                            <div className="widget-toolbar no-border">
                                <Link to="/create-projects" className="btn btn-sm btn-info">
                                    <i className="ace-icon fa fa-plus"></i> Thm mi danh mc project
                                </Link>
                            </div>
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Projects.propTypes = {
    getProjects: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({
    project: state.project
});
export default connect(mapStateToProps, { getProjects })(Projects);
