import RequestService from '../utils/requestService';
import {
    CLEAR_ERRORS,
    NOTES_LOADING,
    GET_ERRORS,
    GET_NOTE,
    DELETE_NOTE
} from './types';
//Add Note
export const createNote = (data, history) => dispatch => {
    RequestService
        .post("/api/notes", data)
        .then(res => history.push(`/show-note_categories/${data.idNoteCategory}`))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Detail Notes
export const getNote = (id) => dispatch => {
    dispatch(setNoteLoading());
    RequestService
        .get(`/api/notes/${id}`)
        .then(res =>
            dispatch({
                type: GET_NOTE,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_NOTE,
                payload: null
            }))
}
//Update Note
export const updateNote = (data, history, id) => dispatch => {
    RequestService
        .put(`/api/notes/${id}`, data)
        .then(res => history.push(`/show-notes/${id}`))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Delete Notes
export const deleteNote = (data, id) => dispatch => {
    RequestService
        .del(`/api/notes/${id}`, data)
        .then(res =>
            dispatch({
                type: DELETE_NOTE,
                payload: id
            }))
        .catch(err =>
            console.log(err.response));
}


//Set loadiing state
export const setNoteLoading = () => {
    return {
        type: NOTES_LOADING
    }
}
//Clear errors
export const clearErrors = () => {
    return {
        type: CLEAR_ERRORS
    }
}

