import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { loginUser } from '../../actions/authActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldLogin from '../common/TextFieldLogin';
import Message from '../../utils/notificationMessage';
import Spinner from '../common/Spinner';
class Login extends Component {
    constructor() {
        super();
        this.state = {
            username: '',
            password: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    componentDidMount() {
        if (this.props.auth.isAuthenticated) {
            this.props.history.push('/');
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
            this.props.history.push('/');
        }
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.errors.message) {
            Message.displayError(nextProps.errors.message);
            this.props.auth.loading = false;
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const userData = {
            username: this.state.username,
            password: this.state.password
        }

        this.props.loginUser(userData);
    }
    render() {
        const { errors } = this.state;
        const { loading } = this.props.auth;
        let loginLoading;
        if (loading) {
            loginLoading = <Spinner />
        } else {
            loginLoading = (
                <div className="clearfix">
                    <button type="submit" className="width-35 pull-right btn btn-sm btn-primary">
                        <i className="ace-icon fa fa-key"></i>
                        <span className="bigger-110">Login</span>
                    </button>
                </div>
            )
        }
        return (
            <div>
                <Breadcrumb name="Login" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            Admin Login
                        </h1>
                    </div>

                    <div className="row">
                        <div className="col-sm-10 col-sm-offset-1">
                            <div className="login-container">
                                <div className="space-6"></div>
                                <div className="position-relative">
                                    <div id="login-box" className="login-box visible widget-box no-border">
                                        <div className="widget-body">
                                            <div className="widget-main">
                                                <h4 className="header blue lighter bigger">
                                                    <i className="ace-icon fa fa-coffee green"></i>
                                                    Please Enter Your Information
											    </h4>
                                                <div className="space-6"></div>
                                                <form onSubmit={this.onSubmit}>
                                                    <fieldset>
                                                        <TextFieldLogin
                                                            placeholder="Username"
                                                            name="username"
                                                            value={this.state.username}
                                                            onChange={this.onChange}
                                                            error={errors.username}
                                                        />
                                                        <TextFieldLogin
                                                            placeholder="Password"
                                                            name="password"
                                                            type="password"
                                                            value={this.state.password}
                                                            onChange={this.onChange}
                                                            error={errors.password}
                                                        />
                                                        <div className="space"></div>
                                                        {loginLoading}
                                                        <div className="space-4"></div>
                                                    </fieldset>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Login.propTypes = {
    loginUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    auth: state.auth,
    errors: state.errors
});

export default connect(mapStateToProps, { loginUser })(Login);
