import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { getNote, updateNote } from '../../actions/noteActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import isEmpty from '../../utils/isEmpty';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';
class EditNote extends Component {
    constructor() {
        super();
        this.state = {
            title: '',
            content: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }
    componentDidMount() {
        this.props.getNote(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.note.note) {
            const note = nextProps.note.note;
            note.title = !isEmpty(note.title) ? note.title : '';
            note.content = !isEmpty(note.content) ? note.content : '';
            this.setState({
                title: note.title,
                content: note.content
            })
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            title: this.state.title,
            content: this.state.content
        }

        this.props.updateNote(Data, this.props.history, this.props.match.params.id);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name={this.state.title} />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            Sa {this.state.title}
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-12">
                                        <TextFieldGroup
                                            label="Tiu "
                                            placeholder="Title"
                                            name="title"
                                            value={this.state.title}
                                            onChange={this.onChange}
                                            error={errors.title}
                                        />
                                        <TextAreaFieldGroup
                                            label="Ni dung"
                                            name="content"
                                            value={this.state.content}
                                            onChange={this.onChange}
                                            error={errors.content}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Sa
                                        </button>
                                        <Link to={`/show-notes/${this.props.match.params.id}`}>
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                            </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
EditNote.propTypes = {
    getNote: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired,
    note: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors,
    note: state.note
});

export default connect(mapStateToProps, { getNote, updateNote })(EditNote);
