import React from 'react'
import { Link } from 'react-router-dom';
export default () => {
    return (
        <div className="footer">
            <div className="footer-inner">
                <div className="footer-content">
                    <Link to="/login">
                        <span className="bigger-120"> <span className="blue bolder">
                            Nguyn Minh Qun </span> &copy;  {new Date().getFullYear()}
                        </span>
                    </Link>
                </div>
            </div>
        </div>
    )
}
