import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { createTimeline } from '../../actions/timelineActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';
import Message from '../../utils/notificationMessage';
class CreateTimeline extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            content: '',
            logo: '',
            date: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.errors.message) {
            Message.displayError(nextProps.errors.message);
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const timelineData = {
            name: this.state.name,
            content: this.state.content,
            date: this.state.date,
            logo: this.state.logo
        }

        this.props.createTimeline(timelineData, this.props.history);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name="To Timeline" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            To Timeline
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <TextFieldGroup
                                            label="Name"
                                            placeholder="Name"
                                            name="name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                        />
                                        <TextAreaFieldGroup
                                            label="Content"
                                            name="content"
                                            value={this.state.content}
                                            onChange={this.onChange}
                                            error={errors.content}
                                        />
                                    </div>
                                    <div className="col-sm-4 form-actions">
                                        <TextFieldGroup
                                            label="Icon fa fa"
                                            placeholder="Icon"
                                            name="logo"
                                            value={this.state.logo}
                                            onChange={this.onChange}
                                            error={errors.logo}
                                        />
                                        <TextFieldGroup
                                            type="date"
                                            label="Date"
                                            placeholder="Date"
                                            name="date"
                                            value={this.state.date}
                                            onChange={this.onChange}
                                            error={errors.date}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Thm mi
                                    </button>
                                        <Link to="/timelines">
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                        </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
CreateTimeline.propTypes = {
    createTimeline: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors
});

export default connect(mapStateToProps, { createTimeline })(CreateTimeline);
