import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { createNote } from '../../actions/noteActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';
class CreateNote extends Component {
    constructor() {
        super();
        this.state = {
            title: '',
            content: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            title: this.state.title,
            content: this.state.content,
            idNoteCategory: this.props.match.params.id
        }

        this.props.createNote(Data, this.props.history);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name="To note" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            To note
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-12">
                                        <TextFieldGroup
                                            label="Tiu "
                                            placeholder="Title"
                                            name="title"
                                            value={this.state.title}
                                            onChange={this.onChange}
                                            error={errors.title}
                                        />
                                        <TextAreaFieldGroup
                                            label="Ni dung"
                                            name="content"
                                            value={this.state.content}
                                            onChange={this.onChange}
                                            error={errors.content}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Thm mi
                                        </button>
                                        <Link to={`/show-note_categories/${this.props.match.params.id}`}>
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                            </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
CreateNote.propTypes = {
    createNote: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors
});

export default connect(mapStateToProps, { createNote })(CreateNote);
