import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { logoutUser } from '../../actions/authActions';
class Sidebar extends Component {
    onLogoutClick(e) {
        e.preventDefault();
        this.props.logoutUser();
    }
    render() {
        const { isAuthenticated } = this.props.auth;
        const authLinks = (
            <ul className="nav nav-list">
                <li>
                    <Link to="/"><i className="menu-icon fa fa-user"></i>
                        <span className="menu-text"> Trang ch </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li>
                    <Link to="/timelines"><i className="menu-icon fa fa-list"></i>
                        <span className="menu-text"> Lch s </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li>
                    <Link to="/note_categories"><i className="menu-icon fa fa-desktop"></i>
                        <span className="menu-text"> Ghi ch </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li>
                    <Link to="/projects"><i className="menu-icon fa fa-code"></i>
                        <span className="menu-text">  n </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li onClick={this.onLogoutClick.bind(this)}>
                    <Link to="/"><i className="menu-icon fa fa-power-off"></i>
                        <span className="menu-text"> ng xut </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
            </ul>
        );
        const guestLinks = (
            <ul className="nav nav-list">
                <li>
                    <Link to="/"><i className="menu-icon fa fa-user"></i>
                        <span className="menu-text"> Profile </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li>
                    <Link to="/experiences"><i className="menu-icon fa fa-code"></i>
                        <span className="menu-text"> Experiences </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li>
                    <Link to="/note_categories"><i className="menu-icon fa fa-desktop"></i>
                        <span className="menu-text"> Blogs </span>
                    </Link>
                    <b className="arrow"></b>
                </li>
                <li>
                    <a target="_blank" href="https://drive.google.com/open?id=1H-tsQKK8rydHxPqP87CmZaLuAXUzdjkg" rel="noopener noreferrer">
                        <i className="menu-icon fa fa-gear"></i>
                        <span className="menu-text"> Download CV </span>
                    </a>
                    <b className="arrow"></b>
                </li>
            </ul>
        );
        return (
            <div>
                {isAuthenticated ? authLinks : guestLinks}
            </div>
        )
    }
}
Sidebar.propTypes = {
    logoutUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    auth: state.auth
});
export default connect(mapStateToProps, { logoutUser })(Sidebar);
