import React from 'react'
import PropTypes from 'prop-types'

const TextCheckboxGroup = ({
    name,
    defaultChecked,
    error,
    onChange,
    label,
    value
}) => {
    return (
        <div className="form-group">
            <label className="col-sm-2 control-label no-padding-right">{label}</label>
            <div className="col-sm-10">
                <input
                    type="checkbox"
                    className="ace ace-switch ace-switch-4"
                    onChange={onChange}
                    name={name}
                    defaultChecked={defaultChecked}
                    value={value}
                />
                {error && <div className="invalid-feedback">{error}</div>}
                <span className="lbl middle"></span>
            </div>
        </div>
    )
};

TextCheckboxGroup.propTypes = {
    name: PropTypes.string.isRequired,
    defaultChecked: PropTypes.bool.isRequired,
    error: PropTypes.string,
    label: PropTypes.string,
    value: PropTypes.bool.isRequired,
    onChange: PropTypes.func.isRequired
}
export default TextCheckboxGroup;