import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
class ExperienceItem extends Component {
    render() {
        const { project } = this.props;
        return (
            <tr>
                <td>{project.name}</td>
                <td><img src={project.image} alt={project.name} width="150px;" height="100px;" /></td>
                <td>
                    <div className="btn-group">
                        <button className="btn btn-xs btn-info">
                            <Link to={`/show-experiences/${project._id}`} style={{ color: '#FFF' }} >
                                <i className="ace-icon fa fa-pencil bigger-120"></i>
                            </Link>
                        </button>
                    </div>
                </td>
            </tr >
        )
    }
}

ExperienceItem.propTypes = {
    project: PropTypes.object.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, null)(ExperienceItem);

