import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getNoteCategory } from '../../actions/noteCategoryActions';
import Spinner from '../common/Spinner';
import NoteFeed from '../note/NoteFeed';
import isEmpty from '../../utils/isEmpty';
class ShowNoteCategory extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            notes: undefined,
            loading: true,
            errors: {}
        };
    }
    componentDidMount() {
        this.props.getNoteCategory(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if ((!isEmpty(nextProps.note_category.note_category))) {
            this.setState({
                loading: false
            })
        }
        if (nextProps.note_category.note_category) {
            const note_category = nextProps.note_category.note_category;
            note_category.name = !isEmpty(note_category.name) ? note_category.name : '';
            note_category.notes = !isEmpty(note_category.notes) ? note_category.notes : undefined;
            this.setState({
                name: note_category.name,
                notes: note_category.notes
            })
        }
    }
    render() {
        const { isAuthenticated } = this.props.auth;
        let Content;
        if (this.state.loading) {
            Content = <Spinner />
        } else if (this.state.notes === undefined) {
            Content = (
                <b>Cha c ni dung</b>
            )
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="80%">Tiu </th>
                            <th width="20%">Link</th>
                        </tr>
                    </thead>

                    <tbody>
                        <NoteFeed notes={this.state.notes} idNoteCategory={this.props.match.params.id} />
                    </tbody>
                </table>
            )
        }
        const authLinks = (
            <div className="widget-toolbar no-border">
                <Link to={`/create-notes/${this.props.match.params.id}`} className="btn btn-sm btn-info">
                    <i className="ace-icon fa fa-plus"></i> Thm mi note
                </Link>
            </div>
        )
        return (
            <div>
                <Breadcrumb name={this.state.name} />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">Danh mc {this.state.name}</h4>
                            {isAuthenticated ? authLinks : null}
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
ShowNoteCategory.propTypes = {
    getNoteCategory: PropTypes.func.isRequired,
    note_category: PropTypes.object.isRequired,
    auth: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    note_category: state.note_category,
    auth: state.auth
});
export default connect(mapStateToProps, { getNoteCategory })(ShowNoteCategory);