import {
    CREATE_NOTE,
    NOTES_LOADING,
    GET_NOTE
} from "../actions/types";

const initialState = {
    notes: [],
    note: [],
    loading: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case NOTES_LOADING:
            return {
                ...state,
                loading: true
            }
        case CREATE_NOTE:
            return {
                ...state,
                notes: [action.payload, ...state.notes]
            }
        case GET_NOTE:
            return {
                ...state,
                note: action.payload,
                loading: false
            }
        default:
            return state;
    }
}
