import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { getNoteCategory, updateNoteCategory } from '../../actions/noteCategoryActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import isEmpty from '../../utils/isEmpty';

class EditNoteCategory extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }
    componentDidMount() {
        this.props.getNoteCategory(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.note_category.note_category) {
            const note_category = nextProps.note_category.note_category;
            note_category.name = !isEmpty(note_category.name) ? note_category.name : '';
            this.setState({
                name: note_category.name
            })
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            name: this.state.name
        }

        this.props.updateNoteCategory(Data, this.props.history, this.props.match.params.id);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name={this.state.name} />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            Sa {this.state.name}
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-12">
                                        <TextFieldGroup
                                            label="Tn danh mc"
                                            placeholder="Name"
                                            name="name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Sa
                                        </button>
                                        <Link to="/note_categories">
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                            </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
EditNoteCategory.propTypes = {
    getNoteCategory: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired,
    note_category: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors,
    note_category: state.note_category
});

export default connect(mapStateToProps, { getNoteCategory, updateNoteCategory })(EditNoteCategory);
