import React, { Component } from 'react'
import PropTypes from 'prop-types'
import NoteItem from './NoteItem'
import { connect } from 'react-redux';

class NoteFeed extends Component {
    render() {
        const { notes, idNoteCategory } = this.props;
        const { isAuthenticated } = this.props.auth;
        if (isAuthenticated) {
            return notes.map(note => <NoteItem key={note._id} note={note} idNoteCategory={idNoteCategory} showActions={true} />)
        } else {
            return notes.map(note => <NoteItem key={note._id} note={note} idNoteCategory={idNoteCategory} showActions={false} />)
        }

    }
}

NoteFeed.propTypes = {
    notes: PropTypes.array.isRequired,
    auth: PropTypes.object.isRequired
}
const mapStateToProps = (state) => ({
    auth: state.auth
});
export default connect(mapStateToProps, null)(NoteFeed);