import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { deleteWork } from '../../actions/workActions';
import { changeWork } from '../../actions/projectActions';
import Moment from 'react-moment';

class WorkItem extends Component {
    onDeleteClick(id) {
        const Data = {
            idProject: this.props.idProject
        }
        if (window.confirm("Bn c mun xa khng ?")) {
            this.props.deleteWork(Data, id);
        }
    }
    onStatusClick(id) {
        this.props.changeWork(id, this.props.idProject);
    }
    render() {
        const { work } = this.props;
        var status;
        if (work.status) {
            status = <td><span className="badge badge-success" style={{ cursor: 'pointer' }} onClick={this.onStatusClick.bind(this, work._id)}> xong</span></td>;
        } else {
            status = <td><span className="badge badge-danger" style={{ cursor: 'pointer' }} onClick={this.onStatusClick.bind(this, work._id)}>Cha xong</span></td >
        }
        return (
            <tr>
                <td>{work.name}</td>
                <td>{<Moment format="DD/MM/YYYY">{work.date}</Moment>}</td>
                {status}
                <td>
                    <div className="hidden-sm hidden-xs btn-group">
                        <button className="btn btn-xs btn-info">
                            <Link to={`/edit-works/${work._id}`} style={{ color: '#FFF' }} >
                                <i className="ace-icon fa fa-pencil bigger-120"></i>
                            </Link>
                        </button>
                        <button className="btn btn-xs btn-danger">
                            <i className="ace-icon fa fa-trash-o bigger-120" onClick={this.onDeleteClick.bind(this, work._id)}></i>
                        </button>
                    </div>
                </td>
            </tr >
        )
    }
}

WorkItem.propTypes = {
    work: PropTypes.object.isRequired,
    deleteWork: PropTypes.func.isRequired,
    changeWork: PropTypes.func.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { deleteWork, changeWork })(WorkItem);

