import {
    CREATE_WORK,
    WORKS_LOADING,
    GET_WORK
} from "../actions/types";

const initialState = {
    works: [],
    work: [],
    loading: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case WORKS_LOADING:
            return {
                ...state,
                loading: true
            }
        case CREATE_WORK:
            return {
                ...state,
                works: [action.payload, ...state.works]
            }
        case GET_WORK:
            return {
                ...state,
                work: action.payload,
                loading: false
            }
        default:
            return state;
    }
}
