import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { deleteProject, changeProject } from '../../actions/projectActions';
import Moment from 'react-moment';

class ProjectItem extends Component {
    onDeleteClick(id) {
        if (window.confirm("Bn c mun xa khng ?")) {
            this.props.deleteProject(id);
        }
    }
    onStatusClick(id) {
        this.props.changeProject(id);
    }
    render() {
        const { project } = this.props;
        var status;
        if (project.status) {
            status = <td><span className="badge badge-success" style={{ cursor: 'pointer' }} onClick={this.onStatusClick.bind(this, project._id)}> xong</span></td>;
        } else {
            status = <td><span className="badge badge-danger" style={{ cursor: 'pointer' }} onClick={this.onStatusClick.bind(this, project._id)}>Cha xong</span></td >
        }
        return (
            <tr>
                <td>{project.name}</td>
                <td>{<Moment format="DD/MM/YYYY">{project.date}</Moment>}</td>
                {status}
                <td>
                    <div className="hidden-sm hidden-xs btn-group">
                        <button className="btn btn-xs btn-success">
                            <Link to={`/show-projects/${project._id}`} style={{ color: '#FFF' }} >
                                <i className="ace-icon fa fa-eye bigger-120"></i>
                            </Link>
                        </button>
                        <button className="btn btn-xs btn-info">
                            <Link to={`/edit-projects/${project._id}`} style={{ color: '#FFF' }} >
                                <i className="ace-icon fa fa-pencil bigger-120"></i>
                            </Link>
                        </button>
                        <button className="btn btn-xs btn-danger">
                            <i className="ace-icon fa fa-trash-o bigger-120" onClick={this.onDeleteClick.bind(this, project._id)}></i>
                        </button>
                    </div>
                </td>
            </tr >
        )
    }
}

ProjectItem.propTypes = {
    project: PropTypes.object.isRequired,
    deleteProject: PropTypes.func.isRequired,
    changeProject: PropTypes.func.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { deleteProject, changeProject })(ProjectItem);

