import {
    CREATE_NOTECATEGORY,
    NOTECATEGORIES_LOADING,
    GET_NOTECATEGORIES,
    GET_NOTECATEGORY,
    DELETE_NOTECATEGORY,
    DELETE_NOTE
} from "../actions/types";

const initialState = {
    note_categories: [],
    note_category: [],
    loading: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case NOTECATEGORIES_LOADING:
            return {
                ...state,
                loading: true
            }
        case CREATE_NOTECATEGORY:
            return {
                ...state,
                note_categories: [action.payload, ...state.note_categories]
            }
        case GET_NOTECATEGORIES:
            return {
                ...state,
                note_categories: action.payload,
                loading: false
            }
        case GET_NOTECATEGORY:
            return {
                ...state,
                note_category: action.payload,
                loading: false
            }
        case DELETE_NOTECATEGORY:
            return {
                ...state,
                note_categories: state.note_categories.filter(note_category => note_category._id !== action.payload)
            }
        case DELETE_NOTE:
            return {
                ...state,
                note_category: state.note_category.notes.filter(notes => notes._id !== action.payload)
            }
        default:
            return state;
    }
}
