import React, { Component } from 'react'
import PropTypes from 'prop-types'
import NoteCategoryItem from './NoteCategoryItem'
import { connect } from 'react-redux';

class NoteCategoryFeed extends Component {
    render() {
        const { isAuthenticated } = this.props.auth;
        const { note_categories } = this.props;
        if (isAuthenticated) {
            return note_categories.map(note_category => <NoteCategoryItem key={note_category._id} note_category={note_category} showActions={true} />)
        } else {
            return note_categories.map(note_category => <NoteCategoryItem key={note_category._id} note_category={note_category} showActions={false} />)
        }
    }
}

NoteCategoryFeed.propTypes = {
    note_categories: PropTypes.array.isRequired,
    auth: PropTypes.object.isRequired
}
const mapStateToProps = (state) => ({
    auth: state.auth
});
export default connect(mapStateToProps, null)(NoteCategoryFeed);