import {
    CREATE_TIMELINE,
    TIMELINES_LOADING,
    GET_TIMELINES,
    GET_TIMELINE,
    DELETE_TIMELINE
} from "../actions/types";

const initialState = {
    timelines: [],
    timeline: [],
    loading: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case TIMELINES_LOADING:
            return {
                ...state,
                loading: true
            }
        case CREATE_TIMELINE:
            return {
                ...state,
                timelines: [action.payload, ...state.timelines]
            }
        case GET_TIMELINES:
            return {
                ...state,
                timelines: action.payload,
                loading: false
            }
        case GET_TIMELINE:
            return {
                ...state,
                timeline: action.payload,
                loading: false
            }
        case DELETE_TIMELINE:
            return {
                ...state,
                timelines: state.timelines.filter(timeline => timeline._id !== action.payload)
            }
        default:
            return state;
    }
}
