import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getProject } from '../../actions/projectActions';
import Spinner from '../common/Spinner';
import WorkFeed from '../work/WorkFeed';
import isEmpty from '../../utils/isEmpty';
class ShowProject extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            works: undefined,
            loading: true,
            errors: {}
        };
    }
    componentDidMount() {
        this.props.getProject(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if ((!isEmpty(nextProps.project.project))) {
            this.setState({
                loading: false
            })
        }
        if (nextProps.project.project) {
            const project = nextProps.project.project;
            project.name = !isEmpty(project.name) ? project.name : '';
            project.works = !isEmpty(project.works) ? project.works : undefined;
            this.setState({
                name: project.name,
                works: project.works
            })
        }
    }
    render() {
        let Content;
        if (this.state.loading) {
            Content = <Spinner />
        } else if (this.state.works === undefined) {
            Content = (
                <b>Cha c ni dung</b>
            )
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="25%">Tn</th>
                            <th width="25%">Ngy</th>
                            <th width="25%">Trng thi</th>
                            <th width="25%"></th>
                        </tr>
                    </thead>

                    <tbody>
                        <WorkFeed works={this.state.works} idProject={this.props.match.params.id} />
                    </tbody>
                </table>
            )
        }
        return (
            <div>
                <Breadcrumb name={this.state.name} />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title"> n {this.state.name}</h4>
                            <div className="widget-toolbar no-border">
                                <Link to={`/create-works/${this.props.match.params.id}`} className="btn btn-sm btn-info">
                                    <i className="ace-icon fa fa-plus"></i> Thm mi cng vic
                                </Link>
                            </div>
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
ShowProject.propTypes = {
    getProject: PropTypes.func.isRequired,
    project: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    project: state.project
});
export default connect(mapStateToProps, { getProject })(ShowProject);