import RequestService from '../utils/requestService';
import {
    CLEAR_ERRORS,
    TIMELINES_LOADING,
    GET_ERRORS,
    GET_TIMELINES,
    GET_TIMELINE,
    DELETE_TIMELINE
} from './types';
//Add Timeline
export const createTimeline = (data, history) => dispatch => {
    RequestService
        .post("/api/timelines", data)
        .then(res => history.push('/timelines'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Get Timelines
export const getTimelines = () => dispatch => {
    dispatch(setTimelineLoading());
    RequestService
        .get('/api/timelines')
        .then(res =>
            dispatch({
                type: GET_TIMELINES,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_TIMELINES,
                payload: null
            }))
}
//Detail Timelines
export const getTimeline = (id) => dispatch => {
    dispatch(setTimelineLoading());
    RequestService
        .get(`/api/timelines/${id}`)
        .then(res =>
            dispatch({
                type: GET_TIMELINE,
                payload: res.data.data
            }))
        .catch(err =>
            dispatch({
                type: GET_TIMELINE,
                payload: null
            }))
}
//Update Timeline
export const updateTimeline = (data, history, id) => dispatch => {
    RequestService
        .put(`/api/timelines/${id}`, data)
        .then(res => history.push('/timelines'))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}
//Delete Timelines
export const deleteTimeline = id => dispatch => {
    RequestService
        .del(`/api/timelines/${id}`)
        .then(res =>
            dispatch({
                type: DELETE_TIMELINE,
                payload: id
            }))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}


//Set loadiing state
export const setTimelineLoading = () => {
    return {
        type: TIMELINES_LOADING
    }
}
//Clear errors
export const clearErrors = () => {
    return {
        type: CLEAR_ERRORS
    }
}

