import React, { Component } from 'react'
import PropTypes from 'prop-types'
import TimelineItem from './TimelineItem'
import { connect } from 'react-redux';

class TimelineFeed extends Component {
    render() {
        const { isAuthenticated } = this.props.auth;
        const { timelines } = this.props;
        if(isAuthenticated) {
            return timelines.map(timeline => <TimelineItem key={timeline._id} timeline={timeline} showActions={true} />)
        } else {
            return timelines.map(timeline => <TimelineItem key={timeline._id} timeline={timeline} showActions={false} />)
        }
    }
}

TimelineFeed.propTypes = {
    timelines: PropTypes.array.isRequired,
    auth: PropTypes.object.isRequired
}
const mapStateToProps = (state) => ({
    auth: state.auth
});
export default connect(mapStateToProps, null)(TimelineFeed);
