import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { createProject } from '../../actions/projectActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import TextCheckboxGroup from '../common/TextCheckboxGroup';
import placeholder from '../../img/placeholder.png'
import TextImage from '../common/TextImage';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';
class CreateProject extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            date: '',
            teamsize: '',
            image: placeholder,
            link: '',
            status: false,
            company: '',
            technology: '',
            content: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        if (e.target.type === 'checkbox') {
            this.setState({
                status: e.target.checked
            })
        } else {
            this.setState({ [e.target.name]: e.target.value });
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            name: this.state.name,
            date: this.state.date,
            status: this.state.status,
            image: this.state.image,
            company: this.state.company,
            teamsize: this.state.teamsize,
            link: this.state.link,
            content: this.state.content,
            technology: this.state.technology
        }

        this.props.createProject(Data, this.props.history);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name="To danh mc  n" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            To danh mc project
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <TextFieldGroup
                                            label="Tn project"
                                            placeholder="Name"
                                            name="name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                        />
                                        <TextFieldGroup
                                            label="Tn cng ty"
                                            placeholder="Company"
                                            name="company"
                                            value={this.state.company}
                                            onChange={this.onChange}
                                            error={errors.company}
                                        />
                                        <TextFieldGroup
                                            label="Cng ngh s dng"
                                            placeholder="Technology"
                                            name="technology"
                                            value={this.state.technology}
                                            onChange={this.onChange}
                                            error={errors.technology}
                                        />
                                        <TextFieldGroup
                                            label="Link Project"
                                            placeholder="Link"
                                            name="link"
                                            value={this.state.link}
                                            onChange={this.onChange}
                                            error={errors.link}
                                        />
                                        <TextAreaFieldGroup
                                            label="Ni dung"
                                            name="content"
                                            value={this.state.content}
                                            onChange={this.onChange}
                                            error={errors.content}
                                        />
                                    </div>
                                    <div className="col-sm-4 form-actions">
                                        <TextFieldGroup
                                            type="date"
                                            label="Ngy lm project"
                                            placeholder="Date Start"
                                            name="date"
                                            value={this.state.date}
                                            onChange={this.onChange}
                                            error={errors.date}
                                        />
                                        <TextFieldGroup
                                            type="number"
                                            label="S lng team"
                                            placeholder="Teamsize"
                                            name="teamsize"
                                            value={this.state.teamsize}
                                            onChange={this.onChange}
                                            error={errors.teamsize}
                                        />
                                        <TextFieldGroup
                                            label="nh project"
                                            placeholder="Image"
                                            name="image"
                                            value={this.state.image}
                                            onChange={this.onChange}
                                            error={errors.image}
                                        />
                                        <TextImage
                                            label="nh"
                                            image={this.state.image}
                                            alt={this.state.name}
                                        />
                                        <TextCheckboxGroup
                                            label="Trng thi"
                                            name="status"
                                            defaultChecked={this.state.status}
                                            onChange={this.onChange}
                                            error={errors.status}
                                            value={this.state.status}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Thm mi
                                    </button>
                                        <Link to="/projects">
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                        </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
CreateProject.propTypes = {
    createProject: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors
});

export default connect(mapStateToProps, { createProject })(CreateProject);
