import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getNoteCategories } from '../../actions/noteCategoryActions';
import Spinner from '../common/Spinner';
import NoteCategoryFeed from './NoteCategoryFeed';
class NoteCategories extends Component {
    componentDidMount() {
        this.props.getNoteCategories();
    }
    render() {
        const { note_categories, loading } = this.props.note_category;
        const { isAuthenticated } = this.props.auth;
        let Content;
        if (note_categories === null || loading) {
            Content = <Spinner />
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="80%">Tn</th>
                            <th width="20%">Link</th>
                        </tr>
                    </thead>

                    <tbody>
                        <NoteCategoryFeed note_categories={note_categories} />
                    </tbody>
                </table>
            )
        }
        const authLinks = (
            <div className="widget-toolbar no-border">
                <Link to="/create-note_categories" className="btn btn-sm btn-info">
                    <i className="ace-icon fa fa-plus"></i> Thm mi danh mc blog
                </Link>
            </div>
        )
        return (
            <div>
                <Breadcrumb name="Ghi ch" />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">Danh mc blog</h4>
                            {isAuthenticated ? authLinks : null}
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
NoteCategories.propTypes = {
    getNoteCategories: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    note_category: state.note_category,
    auth: state.auth
});
export default connect(mapStateToProps, { getNoteCategories })(NoteCategories);
