import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { getWork, updateWork } from '../../actions/workActions';
import Breadcrumb from '../common/Breadcrumb';
import TextFieldGroup from '../common/TextFieldGroup';
import isEmpty from '../../utils/isEmpty';

class EditWork extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            date: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        if (e.target.type === 'checkbox') {
            this.setState({
                status: e.target.checked
            })
        } else {
            this.setState({ [e.target.name]: e.target.value });
        }
    }
    componentDidMount() {
        this.props.getWork(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.work.work) {
            const work = nextProps.work.work;
            work.name = !isEmpty(work.name) ? work.name : '';
            work.date = !isEmpty(work.date) ? new Date(work.date).toISOString().split('T')[0] : '';
            this.setState({
                name: work.name,
                date: work.date
            })
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const Data = {
            name: this.state.name,
            date: this.state.date,
            status: this.state.status
        }

        this.props.updateWork(Data, this.props.history, this.props.match.params.id);
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <Breadcrumb name={this.state.name} />
                <div className="page-content">
                    <div className="page-header">
                        <h1>
                            Sa {this.state.name}
                        </h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <TextFieldGroup
                                            label="Tn cng vic"
                                            placeholder="Name"
                                            name="name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                        />
                                    </div>
                                    <div className="col-sm-4 form-actions">
                                        <TextFieldGroup
                                            type="date"
                                            label="Ngy"
                                            placeholder="Date Start"
                                            name="date"
                                            value={this.state.date}
                                            onChange={this.onChange}
                                            error={errors.date}
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button className="btn btn-info" type="submit" >
                                            <i className="ace-icon fa fa-check bigger-110"></i>
                                            Sa
                                        </button>
                                        <Link to="/projects">
                                            <button className="btn btn-warning">
                                                <i className="ace-icon fa fa-close bigger-110"></i>
                                                Hy
                                            </button>
                                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>

        )
    }
}
EditWork.propTypes = {
    getWork: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired,
    work: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    errors: state.errors,
    work: state.work
});

export default connect(mapStateToProps, { getWork, updateWork })(EditWork);
